import React from "react";
import Layout from "../components/Layout/LayoutPg";

const Policy = () => {
  return (
    <Layout>
      <div className="row contactus mt-5">
        <div className="col-md-6 ">
          <img
            src="/images/contactus.jpeg"
            alt="contactus"
            style={{ width: "100%" }}
          />
        </div>
        <div className="col-md-4">
          <p><b>Information Collection: </b>We collect personal information voluntarily provided by users during account registration and order processing.</p>
          <p><b>Data Usage: </b>The collected information is used to process orders, personalize user experiences, and improve our services.</p>
          <p><b>Third-Party Sharing: </b>We may share user data with trusted third-party service providers to fulfill orders and improve website functionality.</p>
          <p><b>Legal Compliance: </b>We may disclose personal information to comply with legal requirements or protect our rights and users' safety.</p>
        </div>
      </div>
    </Layout>
  );
};

export default Policy;