import React from "react";
import Layout from "../components/Layout/LayoutPg";

const About = () => {
  return (
    <Layout>
      <div className="row contactus mt-5">
        <div className="col-md-6 ">
          <img
            src="/images/about.jpeg"
            alt="contactus"
            style={{ width: "100%" }}
          />
        </div>
        <div className="col-md-4">
          <p className="text-justify mt-2">
          <b>Our Story: </b>At Dream Loot, we started with a vision to create an online marketplace that offers a seamless shopping experience, bringing together quality products from diverse categories.
          </p>
          <p className="text-justify mt-2">
          <b>Passion for Excellence: </b>We are passionate about delivering exceptional products and services to our customers, ensuring their satisfaction remains our top priority.
          </p>
          <p className="text-justify mt-2">
          <b>Curated Selection: </b>Our team meticulously curates a wide range of products, ensuring every item meets our stringent quality standards and reflects the latest trends.
          </p>
        </div>
      </div>
    </Layout>
  );
};

export default About;