import React from 'react'
import Header from './HeaderPg';
import Footer from './FooterPg';
// import {ToastContainer} from "react-toastify";
// import "react-toastify/dist/ReactToastify.css";
import { Toaster } from 'react-hot-toast';

const Layout = ({children}) => {
    return (
        <div>
            <Header/>
            {/* <main style= {{minHeight: "70vh" , marginTop: "70px"}}> */}
            <main style= {{minHeight: "70vh"}}>
                <Toaster/>
                {/* jis bhi chij ko <Layout></Layout> tag ke beech me dalenge to vo children ki jagah show hone lag jaegi */}
                {/* layout me humne upr header rkha h or neeche footer or bs beech ka content change hoga app.js se */}
                {children}
            </main>
            <Footer/>
        </div>
    )
}

export default Layout;
